const api = require('../../config/api.js');
const util = require('../../utils/util.js')
const user = require("../../services/user.js")
const app = getApp()	
export default {
	data() {
		return {
			userInfo: {},
			content: ''
		}
	},
	//初始化
	onLoad() {
		
	},
	// 页面显示
	onShow() {
		this.getInfo()
	},
	
	onShareAppMessage: function () {
		wx.showShareMenu({
			withShareTicket:true
		})
		let code = wx.getStorageSync('code');
		this.setCode(code)
		return {
			title: '@你：这里每天更新好多免费的抽奖，快来抽奖吧~',
			path: '/pages/index/index?code='+code,
			imageUrl: '../../static/images/shareImage.jpg'
		}
	},
	methods: {
		//获取个人中心信息
		getInfo() {
			util.request(api.getUserInfo,{},'get').then(res=>{
				if(res.code == 0) {
					this.userInfo = res.data.user_id;
					this.getBlueTap();
					this.getShareTicket()
				}
			})
		},
		//参与晒图
		partBlueTap() {
			wx.showModal({
				title: '提示',
				content: '抱歉您未中奖，无法晒图。'
			})
		},
		
		//获取幸福晒图区内容
		getBlueTap() {
			wx.showLoading()
			util.request(api.getArticleInfo,{id: 2},'post').then(res=>{
				wx.hideLoading()
				if(res.code == 0) {
					this.content = res.data.content;
				}
			})
		},
		
		//获取分享数据得到的群信息
		getShareTicket() {
			let ticket = wx.getStorageSync('ticket');
			let pages = getCurrentPages();
			let { options } = pages.pop();
			var that = this;
			if(ticket && options.code != "") {
				wx.getShareInfo({
					shareTicket: ticket,
					success:function(res) {
						let params = {
							code: options.code ? options.code : "",
							iv: res.iv,
							data: res.encryptedData
						}	
						console.log(params)
						that.getShareInfos(params)
					}
				})
			}
		},
		
		//新增分享记录
		getShareInfos(params) {
			util.request(api.setShareRechge, params,'post').then(res=>{
				console.log(res)
			})
		},
		//传递分享code进去
		setCode(code) {
			util.request(api.setShareInfo,{code: code},'post').then(res=>{})
		}
		
	},
	filters:{
		//处理html
		formatRichText(html) {
			let newContent= html.replace(/<img[^>]*>/gi,function(match,capture){
				match = match.replace(/style="[^"]+"/gi, '').replace(/style='[^']+'/gi, '');
				match = match.replace(/width="[^"]+"/gi, '').replace(/width='[^']+'/gi, '');
				match = match.replace(/height="[^"]+"/gi, '').replace(/height='[^']+'/gi, '');
				return match;
			});
			newContent = newContent.replace(/style="[^"]+"/gi,function(match,capture){
				match = match.replace(/width:[^;]+;/gi, 'max-width:100%;').replace(/width:[^;]+;/gi, 'max-width:100%;');
				return match;
			});
			newContent = newContent.replace(/<br[^>]*\/>/gi, '');
			newContent = newContent.replace(/\<img/gi, '<img style="max-width:100%;height:auto;display:inline-block;margin:10rpx auto;"');
			return newContent;
		}
	}
}
